<template>
<div class="root">
  <div v-for="item in displayText">
    <DisplayQA :Question=item.Question :Answer=item.Answer />
  </div>
</div>
</template>

<script>
import DisplayQA from "../../components/DisplayQA";
export default {
  name: "Flow",
  data(){
    return {
      displayText: [
        {
          Question: 'Q: 博乐科技2022届校园招聘面向对象是哪些？',
          Answer:   'A: 2021年9月-2022年9月期间毕业的同学。'
        },
        {
          Question: 'Q: 投递过实习生或其他项目岗位，是否可以再次投递？',
          Answer:   'A: 仍可重新投递，过往面试结果不影响本次校招'
        },
        {
          Question: 'Q: 如何了解我的招聘进展呢？',
          Answer:   'A: 如有笔试及面试安排会通过短信及邮件通知，此外，同学可以登录牛客系统或公众号"牛客招聘助手"，查询当前进度。'
        }
      ]
    }
  },
  components:{
    DisplayQA
  }
}
</script>

<style scoped>

</style>



